import React, { useState, useEffect } from 'react'
import axios from 'axios'
import './App.css';
import WebSockett from "./WebSocket.js";
import { Line } from 'react-chartjs-2';




function DataFetching() {
    const [posts, setPosts] = useState([])
    const [bb, setBB] = useState([])
    const [last, setLast] = useState([])
    const [lastbb, setLastbb] = useState([])
    // const [dolar, setDolar] = useState([])



    useEffect(() => {
        let x = 0;
        let xbb = 0;
        setInterval(() => {

            // axios.get('https://www.bitstamp.net/api/v2/ticker/btcusd/')
            //     .then(res => {
            //         //console.log(res)
            //         setPosts(res.data.last)
            //         x = res.data.last;
            //     })
            //     .catch(err => {
            //         console.log(err)
            //     })

            axios.get('https://api.bitbay.net/rest/trading/ticker/BTC-PLN')
                .then(res => {
                    //console.log(res.data.ticker.rate)
                    setBB(res.data.ticker.rate)
                    xbb = res.data.ticker.rate;
                })
                .catch(err => {
                    //console.log(err)
                })

            // axios.get('https://api.nbp.pl/api/exchangerates/rates/a/usd/last/1/?format=json')
            //     .then(res => {
            //         //console.log(res.data.rates[0].mid)
            //         setDolar(res.data.rates[0].mid)
            //     })
            //     .catch(err => {
            //         // console.log(err)
            //     })

            setLast(x);
            setLastbb(xbb);

        }, 30000);


    }, [])



    return (
        <div className="exchange">
            <div className="ex1">
                BitBay
            <div className="BitStamp">
                    {(bb * 1).toFixed(0)} <br />
                    {(((bb - lastbb) / bb) * 100).toFixed(2) >= 0 ? <span class="green">{(((bb - lastbb) / bb) * 100).toFixed(2)}%</span> : <span class="red">{(((bb - lastbb) / bb) * 100).toFixed(2)}%</span>}
                </div>
            </div>

            <div className="ex1">
                Bitstamp
            <div className="BitStamp">
                    <WebSockett />  <br />

                    {(((posts - last) / posts) * 100).toFixed(2) >= 0 ? <span class="green">{(((posts - last) / posts) * 100).toFixed(2)}%</span> : <span class="red">{(((posts - last) / posts) * 100).toFixed(2)}%</span>}
                </div>
            </div>
        </div>





    )
}

export default DataFetching
